<template>
  <div id="app" @mousemove="dragPicker" @mouseleave="selectEnd" @mouseup="selectEnd">
    <main>
      <div class="check__wrapper" :style="{backgroundColor:'#' + rgb2hex(rgb)}">
        <div class="check-1" :style="{backgroundColor:'#' + rgb2hex(triad[0].rgb)}"></div>
        <div class="check-2" :style="{backgroundColor:'#' + rgb2hex(triad[2].rgb)}"></div>
        <div class="picked-check"></div>
      </div>
      <div class="picked__wrapper clearfix">
        <div class="picked__color color__elm" :style="{backgroundColor:'#' + rgb2hex(rgb)}"></div>
        <div class="picked__value-wrapper">
          <div>
            <span class="picked__value__title">HEX</span>
            <span>#<input :value="rgb2hex(rgb)" @blur="hexInput" @keyup.13="hexInput" @focus="focusAndSelect"></span>
          </div>
          <div>
            <span class="picked__value__title">RGB</span>
            <span>(
              <input type="text" :value="rgb.r" @keydown="colorNumInput('r', arguments[0])" @blur="colorNumInput('r', arguments[0])">,
              <input type="text" :value="rgb.g" @keydown="colorNumInput('g', arguments[0])" @blur="colorNumInput('g', arguments[0])">,
              <input type="text" :value="rgb.b" @keydown="colorNumInput('b', arguments[0])" @blur="colorNumInput('b', arguments[0])">
            )</span>
          </div>
          <div>
            <span class="picked__value__title">HSV</span>
            <span>(
              <input type="text" :value="hsv.h" @keydown="colorNumInput('h', arguments[0])" @blur="colorNumInput('h', arguments[0])">,
              <input type="text" :value="hsv.s" @keydown="colorNumInput('s', arguments[0])" @blur="colorNumInput('s', arguments[0])">,
              <input type="text" :value="hsv.v" @keydown="colorNumInput('v', arguments[0])" @blur="colorNumInput('v', arguments[0])">
            )</span>
          </div>
        </div>
      </div>
      <div class="pickers__wrapper clearfix">
        <div class="picker-container" :style="{width: this.colorPickerSize + 'px'}">
          <div id="color-area" class="colorpicker" @mousedown="colorSelect" :style="pickerStyle">
            <div :style="{left:colorCursor.left + 'px',top:colorCursor.top + 'px'}" class="colorpicker-cursor"></div>
          </div>

          <div id="huepicker" class="huepicker" :style="huePickerStyle" @mousedown="hueSelectStart" @mousemove="dragPicker">
            <div class="huepicker-cursor" :style="{left:(hueCursorPos - 8) + 'px'}"></div>
          </div>
        </div>
        <div class="saved-colors__wrapper">
          <table>
            <tr>
              <td>
                <div class="saved-colors__color" :style="{backgroundColor:'#' + rgb2hex(rgb)}" @click="addFavorite({rgb: rgb, hsv: hsv})"></div>
              </td>
              <td>#{{ rgb2hex(rgb) }}</td>
              <td>rgb({{rgb.r}}, {{rgb.g}}, {{rgb.b}})</td>
            </tr>
            <tr><td>complementary</td></tr>
            <tr
              v-for="color in this.complementary"
            >
              <td>
                <div class="saved-colors__color" :style="{backgroundColor:'#' + rgb2hex(color.rgb)}"></div>
              </td>
              <td>#{{ rgb2hex(color.rgb) }}</td>
              <td>rgb({{color.rgb.r}}, {{color.rgb.g}}, {{color.rgb.b}})</td>
            </tr>
            <tr><td>analogous</td></tr>
            <tr
              v-for="color in this.analogous"
            >
              <td>
                <div class="saved-colors__color" :style="{backgroundColor:'#' + rgb2hex(color.rgb)}"></div>
              </td>
              <td>#{{ rgb2hex(color.rgb) }}</td>
              <td>rgb({{color.rgb.r}}, {{color.rgb.g}}, {{color.rgb.b}})</td>
            </tr>
            <tr><td>triad</td></tr>
            <tr
              v-for="color in this.triad"
            >
              <td>
                <div class="saved-colors__color" :style="{backgroundColor:'#' + rgb2hex(color.rgb)}"></div>
              </td>
              <td>#{{ rgb2hex(color.rgb) }}</td>
              <td>rgb({{color.rgb.r}}, {{color.rgb.g}}, {{color.rgb.b}})</td>
            </tr>
            <tr>
              <td>splitComplement</td>
            </tr>
            <tr
              v-for="color in this.splitComplement"
            >
              <td>
                <div class="saved-colors__color" :style="{backgroundColor:'#' + rgb2hex(color.rgb)}"></div>
              </td>
              <td>#{{ rgb2hex(color.rgb) }}</td>
              <td>rgb({{color.rgb.r}}, {{color.rgb.g}}, {{color.rgb.b}})</td>
            </tr>
            <tr><td>favoriteColors</td></tr>
            <tr
              v-for="color in this.favoriteColors"
            >
              <td>
                <div class="saved-colors__color" :style="{backgroundColor:'#' + rgb2hex(color.rgb)}"></div>
              </td>
              <td>#{{ rgb2hex(color.rgb) }}</td>
              <td>rgb({{color.rgb.r}}, {{color.rgb.g}}, {{color.rgb.b}})</td>
            </tr>
          </table>
        </div>
      </div>
<!--       <adsense
        ad-client="ca-pub-6288892025887848"
        ad-slot="4490014743"
        ad-layout="in-article"
        ad-format="fluid">
      </adsense>
 -->      <!-- <input :value="rgbaCode" @blur="rgbaCodeInput" @keyup.13="rgbaCodeInput" @focus="focusAndSelect"> -->
    </main>
  </div>
</template>

<script>
export default {
  data: function () {
    return {
      rgb: {
        r: 255,
        g: 0,
        b: 0
      },
      hsv: {
        h: 360,
        s: 100,
        v: 100
      },
      colorPickerSize: 200,
      hueChangeFlg: false,
      hueCursorClickFlg: false,
      colorChangeFlg: false,
      colorCursorSize: 16,
      favoriteColors: []
    }
  },
  methods: {
    // お気に入り登録
    addFavorite: function (color) {
      this.favoriteColors.push(color)
    },
    // 色相領域クリック
    hueSelectStart: function (event) {
      this.hueChangeFlg = true
      var ajustY = 0

      if (this.hueCursorClickFlg) {
        ajustY = this.hueCursorPos
        // ajustY = this.hueCursorPos + this.hueCursorSize/2
      }

      this.hsv.h = Math.round(
        Math.abs((event.offsetX + ajustY) * this.hueUnit - 360)
      )
      this.rgb = this.hsv2rgb(this.hsv)
    },
    // 色相カーソルクリック
    hueCursorClick: function () {
      this.hueCursorClickFlg = true
    },
    // カラーピッカークリック
    colorSelect: function (event) {
      this.colorChangeFlg = true
      this.hsv.s = Math.round((event.offsetX) * this.colorUnit)
      this.hsv.v = Math.round(
        Math.abs((event.offsetY) * this.colorUnit - 100)
      )

      this.rgb = this.hsv2rgb(this.hsv)
    },
    // ドラッグ時
    dragPicker: function (event) {
      // console.log(event.pageX)
      // console.log(event.pageY)
      // console.log(event.offsetX)
      // console.log(event.offsetY)

      var colorArea = document.getElementById('color-area')
      var areaY = colorArea.offsetTop
      var areaX = colorArea.offsetLeft

      var hueArea = document.getElementById('huepicker')

      var cx = event.pageX
      var cy = event.pageY
      // var cx = event.offsetX
      // var cy = event.offsetY

      // 色相ドラッグ
      if (this.hueChangeFlg) {
        var hueCursorPosX = cx - hueArea.offsetLeft
        if (hueCursorPosX < 0) {
          hueCursorPosX = 0
        } else if (hueCursorPosX > this.colorPickerSize) {
          hueCursorPosX = this.colorPickerSize
        }
        this.hsv.h = Math.round(Math.abs(this.hueUnit * hueCursorPosX - 360))
        this.rgb = this.hsv2rgb(this.hsv)
      // カラーピッカードラッグ
      } else if (this.colorChangeFlg) {
        var ch = this.colorPickerSize
        var cw = this.colorPickerSize

        var colorCursorPosY = cy - areaY
        // var colorCursorPosY = cy - ct
        if (colorCursorPosY < 0) {
          colorCursorPosY = 0
        } else if (colorCursorPosY > ch) {
          colorCursorPosY = ch
        }
        var colorCursorPosX = cx - areaX
        // var colorCursorPosX = cx - cl
        if (colorCursorPosX < 0) {
          colorCursorPosX = 0
        } else if (colorCursorPosX > cw) {
          colorCursorPosX = cw
        }
        this.hsv.s = Math.round(colorCursorPosX * this.colorUnit)
        this.hsv.v = Math.round(
          Math.abs(colorCursorPosY * this.colorUnit - 100)
        )
        this.rgb = this.hsv2rgb(this.hsv)
      }
    },
    // マウスアップで全フラグを解除
    selectEnd: function (event) {
      this.hueChangeFlg = false
      this.colorChangeFlg = false
      this.hueCursorClickFlg = false
    },
    // 16進数コード入力
    hexInput: function (event) {
      var hex = event.target.value
      hex = hex.replace(/#/, '')
      if (!hex.match(/[A-Fa-f0-9]+/)) {
        return false
      } else {
        if (hex.length !== 6 && hex.length !== 3) {
          return false
        } else {
          if (hex.length === 3) {
            hex =
              hex.charAt(0) +
              hex.charAt(0) +
              hex.charAt(1) +
              hex.charAt(1) +
              hex.charAt(2) +
              hex.charAt(2)
          }
        }
      }
      this.rgb = this.hex2rgb(hex)
      this.hsv = this.rgb2hsv(this.rgb)
    },
    // RGB HSB入力時
    colorNumInput: function (color, event) {
      if (event.type !== 'keydown' && event.type !== 'blur') {
        return false
      } else {
        var val = event.target.value
        if (!val.match(/\d+/)) {
          return false
        }
        val = Number(val)

        if (event.type === 'keydown') {
          if (
            event.key !== 'Enter' &&
            event.key !== 'ArrowUp' &&
            event.key !== 'ArrowDown' &&
            !event.shiftKey
          ) {
            return false
          } else {
            var changeNum = 1
            if (event.shiftKey) {
              changeNum = 10
            }

            if (event.key === 'ArrowUp') {
              val += changeNum
            } else if (event.key === 'ArrowDown') {
              val -= changeNum
            }
          }
        }
        console.log(color)
        if (color === 'r' || color === 'b' || color === 'g') {
          if (val > 255) {
            val = 255
          } else if (val < 0) {
            val = 0
          }
          if (color === 'r') {
            this.rgb.r = val
          } else if (color === 'g') {
            this.rgb.g = val
          } else if (color === 'b') {
            this.rgb.b = val
          }
          this.hsv = this.rgb2hsv(this.rgb)
        } else if (color === 'h' || color === 's' || color === 'v') {
          if (color === 'h') {
            if (val > 360) {
              val = 360
            } else if (val < 0) {
              val = 0
            }
            this.hsv.h = val
          } else if (color === 's') {
            if (val > 100) {
              val = 100
            } else if (val < 0) {
              val = 0
            }
            this.hsv.s = val
          } else if (color === 'v') {
            if (val > 100) {
              val = 100
            } else if (val < 0) {
              val = 0
            }
            this.hsv.v = val
          }
          this.rgb = this.hsv2rgb(this.hsv)
        }
      }
    },
    // rgbaコード入力時
    rgbaCodeInput: function (event) {
      var rgbaCode = event.target.value

      rgbaCode = rgbaCode.replace(/rgba?/, '')
      rgbaCode = rgbaCode.replace(/\(/, '')
      rgbaCode = rgbaCode.replace(/\)/, '')
      rgbaCode = rgbaCode.replace(/ /, '')
      var rgbaCodeAry = rgbaCode.split(',')

      if (rgbaCodeAry.length < 3) {
        return false
      } else {
        if (
          !rgbaCodeAry[0].match(/\d+/) ||
          !rgbaCodeAry[1].match(/\d+/) ||
          !rgbaCodeAry[2].match(/\d+/)
        ) {
          return false
        }
      }

      this.rgb.r = rgbaCodeAry[0]
      this.rgb.g = rgbaCodeAry[1]
      this.rgb.b = rgbaCodeAry[2]
      this.hsv = this.rgb2hsv(this.rgb)
    },

    // テキストボックスフォーカス
    focusAndSelect: function (event) {
      event.target.select()
    }
  },
  computed: {
    // RGBのコード出す
    rgbaCode: function () {
      return rgb => {
        return (
          'rgb(' +
          rgb.r +
          ',' +
          rgb.g +
          ',' +
          rgb.b +
          ')'
        )
      }
    },
    // 16進数→RGB値
    hex2rgb: function () {
      return hex => {
        return {
          r: Math.round(parseInt(hex.slice(0, 2), 16)),
          g: Math.round(parseInt(hex.slice(2, 4), 16)),
          b: Math.round(parseInt(hex.slice(4), 16))
        }
      }
    },
    // RGB→16進数
    rgb2hex: function () {
      return rgb => {
        return (
          ('00' + rgb.r.toString(16)).slice(-2) +
          ('00' + rgb.g.toString(16)).slice(-2) +
          ('00' + rgb.b.toString(16)).slice(-2)
        )
      }
    },
    // RGB→HSV
    rgb2hsv: function () {
      return rgb => {
        var rr
        var gg
        var bb
        var r = rgb.r / 255
        var g = rgb.g / 255
        var b = rgb.b / 255
        var h
        var s
        var v = Math.max(r, g, b)
        var diff = v - Math.min(r, g, b)
        var diffc = function (c) {
          return (v - c) / 6 / diff + 1 / 2
        }
        if (diff === 0) {
          h = s = 0
        } else {
          s = diff / v
          rr = diffc(r)
          gg = diffc(g)
          bb = diffc(b)

          if (r === v) {
            h = bb - gg
          } else if (g === v) {
            h = 1 / 3 + rr - bb
          } else if (b === v) {
            h = 2 / 3 + gg - rr
          }
          if (h < 0) {
            h += 1
          } else if (h > 1) {
            h -= 1
          }
        }

        if (h === 0) {
          h = 1
        }
        return {
          h: Math.round(h * 360),
          s: Math.round(s * 100),
          v: Math.round(v * 100)
        }
      }
    },
    // HSV→RGB
    hsv2rgb: function () {
      return hsv => {
        var h = hsv.h
        var s = hsv.s
        var v = hsv.v

        // var max = v
        // var min = max - s / 255 * max
        var rgb = { r: 0, g: 0, b: 0 }

        if (h === 360) {
          h = 0
        }

        s = s / 100
        v = v / 100

        if (s === 0) {
          rgb.r = Math.round(v * 255)
          rgb.g = Math.round(v * 255)
          rgb.b = Math.round(v * 255)
          return rgb
        }

        var dh = Math.floor(h / 60)
        var p = v * (1 - s)
        var q = v * (1 - s * (h / 60 - dh))
        var t = v * (1 - s * (1 - (h / 60 - dh)))

        switch (dh) {
          case 0:
            rgb.r = v
            rgb.g = t
            rgb.b = p
            break
          case 1:
            rgb.r = q
            rgb.g = v
            rgb.b = p
            break
          case 2:
            rgb.r = p
            rgb.g = v
            rgb.b = t
            break
          case 3:
            rgb.r = p
            rgb.g = q
            rgb.b = v
            break
          case 4:
            rgb.r = t
            rgb.g = p
            rgb.b = v
            break
          case 5:
            rgb.r = v
            rgb.g = p
            rgb.b = q
            break
        }

        rgb.r = Math.round(rgb.r * 255)
        rgb.g = Math.round(rgb.g * 255)
        rgb.b = Math.round(rgb.b * 255)
        return rgb
      }
    },
    complementary: function () {
      var h = this.hsv.h + 180
      if (h > 360)
        h -= 360
      var hsv = {
        h: h,
        s: this.hsv.s,
        v: this.hsv.v
      }
      return [{rgb: this.hsv2rgb(hsv), hsv: hsv}]
    },
    analogous: function () {
      var h = this.hsv.h + 30
      if (h > 360)
        h -= 360

      var h2 = this.hsv.h - 30
      if (h2 < 0)
        h2 += 360
      var hsv = [{
        h: h,
        s: this.hsv.s,
        v: this.hsv.v
      },{
        h: h2,
        s: this.hsv.s,
        v: this.hsv.v
      }]
      return [
        {rgb: this.hsv2rgb(hsv[0]), hsv: hsv[0]},
        {rgb: this.rgb, hsv: this.hsv},
        {rgb: this.hsv2rgb(hsv[1]), hsv: hsv[1]}
      ]
    },
    triad: function () {
      var h = this.hsv.h + 120
      if (h > 360)
        h -= 360

      var h2 = this.hsv.h - 120
      if (h2 < 0)
        h2 += 360
      var hsv = [{
        h: h,
        s: this.hsv.s,
        v: this.hsv.v
      },{
        h: h2,
        s: this.hsv.s,
        v: this.hsv.v
      }]
      return [
        {rgb: this.hsv2rgb(hsv[0]), hsv: hsv[0]},
        {rgb: this.rgb, hsv: this.hsv},
        {rgb: this.hsv2rgb(hsv[1]), hsv: hsv[1]}
      ]
    },
    // tetradics: function () {
    //   var h = this.hsv.h + 30
    //   if (h > 360)
    //     h -= 360

    //   var h2 = this.hsv.h - 30
    //   if (h2 < 0)
    //     h2 += 360
    //   var hsv = [{
    //     h: h,
    //     s: this.hsv.s,
    //     v: this.hsv.v
    //   },{
    //     h: h2,
    //     s: this.hsv.s,
    //     v: this.hsv.v
    //   }]
    //   return [
    //     {rgb: this.hsv2rgb(hsv[0]), hsv: hsv[0]},
    //     {rgb: this.rgb, hsv: this.hsv},
    //     {rgb: this.hsv2rgb(hsv[1]), hsv: hsv[1]}
    //   ]
    // },
    splitComplement: function () {
      var h = this.hsv.h + 150
      if (h > 360)
        h -= 360

      var h2 = this.hsv.h - 150
      if (h2 < 0)
        h2 += 360
      var hsv = [{
        h: h,
        s: this.hsv.s,
        v: this.hsv.v
      },{
        h: h2,
        s: this.hsv.s,
        v: this.hsv.v
      }]
      return [
        {rgb: this.hsv2rgb(hsv[0]), hsv: hsv[0]},
        {rgb: this.rgb, hsv: this.hsv},
        {rgb: this.hsv2rgb(hsv[1]), hsv: hsv[1]}
      ]
    },
    hueUnit: function () {
      return 360 / this.colorPickerSize
    },
    hueCursorPos: function () {
      return Math.round(
        Math.abs(this.hsv.h - 360) / this.hueUnit
        // Math.abs(this.hsv.h - 360) / this.hueUnit - this.hueCursorSize / 2
      )
    },
    colorUnit: function () {
      return 100 / this.colorPickerSize
    },
    colorCursor: function () {
      return {
        left: this.hsv.s / this.colorUnit - this.colorCursorSize / 2,
        top:
          Math.abs(this.hsv.v / this.colorUnit - this.colorPickerSize) -
          this.colorCursorSize / 2
      }
    },
    pickerStyle: function () {
      return {
        backgroundColor: 'hsl(' + this.hsv.h + ',100%,50%)',
        width: this.colorPickerSize + 'px',
        height: this.colorPickerSize + 'px'
      }
    },
    huePickerStyle: function () {
      return {
        width: this.colorPickerSize + 'px'
      }
    }
  }
}
</script>

