.huepicker {
  position: relative;
  height: 30px;
  margin-top: 20px;
  border-radius: 5px;
  background-image: linear-gradient(to right,
    hsl(360,100%,50%),
    hsl(300,100%,50%),
    hsl(240,100%,50%),
    hsl(180,100%,50%),
    hsl(120,100%,50%),
    hsl(60,100%,50%),
    hsl(0,100%,50%)
  );
}
.huepicker-cursor {
  pointer-events: none;
  position: absolute;
  top: -4px;
  box-sizing: content-box;
  height: 100%;
  width: 8px;
  border: 4px solid #666;
  border-radius: 6px;
}
</style>



